p {
  font-size: 0.75rem;
  font-family: var(--ubuntu-mono);
  color: var(--gray-1);
}

button {
  cursor: pointer;
  box-shadow: 0px 2px 3px rgba(51, 51, 51, 0.2);
  border-radius: 6px;
  outline: none;
  border: 1px solid transparent;
  margin-bottom: 40px;
  padding: 9px 18px;
  font-family: var(--open-sans);
  font-weight: var(--medium);
  font-size: 0.875rem;

  transition: all 0.75s;
}

/* DEFAULT BUTTON */
.default {
  background-color: var(--gray-6);
  color: var(--gray-2);
}

.default-hovered-focused {
  background-color: var(--btn-default-state);
  color: var(--gray-2);
}


/* OUTLINE BUTTON */
.outline {
  background-color: transparent;
  border: 1px solid var(--purple);
  color: var(--purple);
}

.outline:hover,
.outline:focus {
  background-color: #2962ff1a;
}


/* TEXT BUTTON */
.text {
  color: var(--purple);
  background-color: transparent;
  box-shadow: none;
}

.text:hover,
.text:focus {
  background-color: #2962ff1a;
}


/* DISABLE SHADOW BUTTON */
.disabled-shadow {
  box-shadow: none;
}


/* DISABLED BUTTON */
.disabled {
  color: var(--gray-5);
  cursor: auto;
}

.default-hovered-focused.disabled:hover,
.default-hovered-focused.disabled:focus {
  background-color: var(--gray-6);
}

.text.disabled:hover,
.text.disabled:focus {
  background-color: transparent;
}


/* ICON BUTTON */
.start-icon {
  margin-right: 10px;
}

.end-icon {
  margin-left: 10px;
}


/* BUTTON SIZES */
.size-sm {
  margin-top: 10px;
  padding: 4.5px 9px;
}

.size-md {
  margin-top: 6px;
}

.size-lg {
  padding: 14px 27px;
}


/* BUTTON COLORS */
.btn-color {
  color: var(--white);
}

.btn-primary {
  background-color: var(--blue);
}

.btn-primary:hover,
.btn-primary:focus {
  background-color: var(--btn-primary-state);
}

.btn-secondary {
  background-color: var(--btn-secondary);
}

.btn-secondary:hover,
.btn-secondary:focus {
  background-color: var(--btn-secondary-state);
}

.btn-danger {
  background-color: var(--btn-danger);
}

.btn-danger:hover,
.btn-danger:focus {
  background-color: var(--btn-danger-state);
}